







































































/*公共样式*/
.bottomBorder[data-v-4c622928]{
		border-bottom: 1px solid #f2f2f2;
		box-shadow:0px 1px 0px #ececec;
}
/*地图*/
.fa-rss[data-v-4c622928]{
		position: absolute;
	    right: 2.0rem;
	    top: 1.7rem;
	    z-index: 3000;
	    color: #fff;
	    font-size: 1rem;
}
.map[data-v-4c622928]{
		height: 15.1rem;
		/*background-color: Red; */
		border-bottom: 1px solid #f0f2ee;
}
.mapTab[data-v-4c622928]{
		height: 4rem;
		overflow: hidden;
		width: 100%;
		margin-bottom: 0.5rem;
		background: #fff;
}
.mapTab ul[data-v-4c622928]{
		height: 3.25rem;
		margin:0.375rem 0;
}
.mapTab ul li[data-v-4c622928]{
		float: left;
		height: 3.25rem;
		width:33.333333%;
		text-align: center;
		box-sizing: border-box;
		border-right: 1px solid #f3f3f3;
		box-shadow: -1px 0px 0px #f9f9f9 inset;
		font-size: 0.65rem;
}
.mapTab ul li span[data-v-4c622928]{
		display: inline-block;
		border-radius: 100%;
		background-color: #000;
		width:2.2rem;
		height: 2.2rem;
		line-height: 2.2rem;
		color:#fff;
}
.mapTab ul li:nth-child(1) span[data-v-4c622928]{
		background-color: #23d155;
}
.mapTab ul li:nth-child(2) span[data-v-4c622928]{
		background-color: #0077ef;
}
.mapTab ul li:nth-child(3) span[data-v-4c622928]{
		background-color: #fa4111;
}
.mapTab ul li span i[data-v-4c622928]{
		font-size: 1rem;
		color:#fff;
	    line-height: 2.2rem;
}
.mapTab ul li p[data-v-4c622928]{
		color:#040404;
		font-size: 0.65rem;
}
.venue[data-v-4c622928]{
		height: 5.15rem;
		background-color: #fff;
}
.venue ul[data-v-4c622928],.vueneIntroduce ul[data-v-4c622928]{
		width: 17rem;
		margin:auto;
}
.venue ul li[data-v-4c622928]:nth-child(1),.vueneIntroduce ul li[data-v-4c622928]:nth-child(1){
		height: 2rem;
		line-height: 2rem;
		text-align: left;
		font-size: 0.7rem;
		color:#0f0f0f;
		box-sizing: border-box;
}
.venue ul li[data-v-4c622928]:nth-child(2){
		height: 3.1rem;
		line-height: 1.8rem;
		text-align: left;
		font-size: 0.7rem;
		color:#484848;
		box-sizing: border-box;
}
.venue ul li i[data-v-4c622928]{
		display: block;
		font-size: 0.6rem;
		color:#7d7d7d;
}
.vueneIntroduce[data-v-4c622928]{
		background-color: #fff;
		overflow: hidden;
}
.vueneIntroduce ul li[data-v-4c622928]:nth-child(2){
		line-height: 1rem;
		text-align: left;
		font-size: 0.7rem;
		color:#343434;
		box-sizing: border-box;
		margin:0.7rem 0;
}




