












































































































































































































/********下拉选择框*************/
.range{
	position: fixed;
    color: #000;
    z-index: 3000;
    right: 1.25rem;
    top: 1.6rem;
}
.popup.mint-popup-top{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	top:3.5rem;
}
.popup .tabChoice.mint-navbar{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.popup .tabChoice .mint-tab-item{
	display: inline;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	border-bottom: 1px solid #e1e1e1;
	color: inherit;
}
.popup .tabChoice .mint-tab-item:nth-child(3),.popup .tabChoice .mint-tab-item:nth-child(4),.popup .tabChoice .mint-tab-item:nth-child(5){
		border-bottom: none;
}
.popup .tabChoice.mint-navbar .mint-tab-item.is-selected{
   color: #c50139;
   border-bottom: none;
   background:#f1f5f4;
}
.popup .content.mint-tab-container{
	-webkit-box-flex: 2;
	    -ms-flex: 2;
	        flex: 2;
	height: 12rem;
	background-color: #f1f5f4;
	position: relative;
}
.popup .content.mint-tab-container .mint-cell{
	background-color: #f1f5f4;
}
.fa-check{
	position: absolute;
	right: 1rem;
	top: 0.7rem;
	font-size: 0.6rem;
	color:#c9164d;
	z-index: 3000;
}
/*选座购买下拉样式*/
.clickTime.mint-popup-top{
	width: 100%;
	top:5.5rem;
	text-align: center;
}
/*.clickTime .mint-tab-container.time {
	display: flex;
	flex-direction: column;
}*/
.clickTime .mint-tab-container.time .mint-tab-container-wrap{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.clickTime .time {
	position: relative;
}
.clickTime .time .mint-cell .mint-cell-text{
	 color: #cb0645;
    font-size: 0.6rem;
}
.clickTime .time i{
	font-size: 0.6rem;
}
/*选座购买头部点击样式*/
.push{
	height: 2rem;
	padding-top: 0.5rem;
	box-sizing: border-box;
	border-bottom: 1px solid #d694b2;
	box-shadow: 0px 1px 0px #ead6d8;
	position: relative;
	z-index: 3000;
	background-color: #fff;
}
.push span{
	color:#cb0645;
	font-size: 0.6rem;
}
.push span.triangle{
/*	border-top:4px solid #cd093a;*/
	border-right: 0.3rem solid transparent;
	border-bottom: 0.4rem solid #cd093a;
	border-left: 0.3rem solid transparent;
    margin-left: 0.25rem;
    margin-top: -2.4rem;
    display: inline-block;
}
.pushDown{
	margin-top: 70px;
    text-align: center;
}
.rotateDown{
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
/*	transition: 0.3s*/
}
.rotateUp{
	-webkit-transform: rotate(0deg);
	        transform: rotate(0deg);
/*	transition: 0.3s*/
}



